@import url(https://fonts.googleapis.com/css?family=Raleway:400,500,600,700,800,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

p {
  /* justify-content: left; */
}

:root {
    --main-accent-color: #03bad5;
    --section-title-color-dark: #def4f5;
    --body-color: #495057;
}
.about-wrap {
    background-color: rgb(37, 39, 52);
    margin-top: -35px;
    padding-top: 55px;
    padding-bottom: 20px;
}
.abt-side {
    /* color: #fff; */
    padding-bottom: 2.5rem;
}

.section-title {
    position: relative;
}

h2.section-title-heading {
    color: #def4f5;
    color: var(--section-title-color-dark);
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1.6rem;
}
.hr{
    border: 1px solid #03bad5;
    border: 1px solid var(--main-accent-color);
    width: 5%;
}
.hr:hover{
    border: 1px solid #03bad5;
    border: 1px solid var(--main-accent-color);
    width: 10%;
    transition: all 0.6s ease-out;
    -webkit-transition: all 0.6s ease-out;
    -moz-transition: all 0.6s ease-out;
    -o-transition: all 0.6s ease-out;
}
@media (max-width: 992px) {
    .hr{
        border: 1px solid #03bad5;
        border: 1px solid var(--main-accent-color);
        width: 20%;
    }
    .hr:hover{
        border: 1px solid #03bad5;
        border: 1px solid var(--main-accent-color);
        width: 30%;
        transition: all 0.6s ease-out;
        -webkit-transition: all 0.6s ease-out;
        -moz-transition: all 0.6s ease-out;
        -o-transition: all 0.6s ease-out;
    }
}

.abt-img{
    padding-bottom: 5px;
}
.abt-wrapper{
    padding-top: 40px;
}

.abt-me {
    text-align: justify;
    font-size: 20px;
    font-weight: 400;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    line-height: 25px;
    padding-bottom: 40px; 
    color: #d4d5d8;
}

.hero {
    /* background: linear-gradient(to right, rgba(49, 49, 54, 0.62), rgba(4, 33, 34, 0.52)), url('../bg-img/p1.jpg') center center/cover no-repeat; */
    background: linear-gradient(to right, rgba(49, 49, 54, 0.62), rgba(4, 33, 34, 0.52)), url(/poetic-nancy/static/media/hero.6795f660.jpg) center center/cover no-repeat;
    /* box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2); */
    height: 80vh;
    color: #fff;
    margin-top: 76px;
}
@media (max-width: 992px) {
    .hero{
        margin-top: 76px;
    }
}
.hero-cont{
    margin-top: 5rem;
}
.hero-content h1 {
    font-family: 'Raleway', sans-serif;
    text-align: center;
    font-size: 2.5rem;
    line-height: 4rem;
    font-weight: 800;
    color: #fff;
    letter-spacing: .15rem;
}
.hero-txt p{
    font-weight: 500;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}


